<template>
  <div class="col-6">
    <div class="panel">
      <div class="panel-heading">
        <div class="panel-title">企业抽检情况-{{activeArea.name}}</div>
      </div>
      <div class="panel-body">
        <div class="chart" id="chart_checked">
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    activeHy: {
      type: Number
    },
    activeArea: {
      type: Object
    }
  },
  data () {
    return {}
  },
  created () {
    this.init()
  },
  methods: {
    init () {
      let self = this
      this.$http.get({
        api: 'A_S_C_CHECKED',
        params: {
          industryId: this.activeHy,
          areaId: this.activeArea.code
        },
        callback: (res) => {
          if (res.length === 0) {
            document.querySelector('#chart_checked').innerHTML = '暂无数据'
          } else {
            self.draw(res)
          }
        }
      })
    },
    draw (a) {
      let xAxis = []
      let qualified = []
      let unqualified = []
      a.map(o => {
        xAxis.push(o.name)
        qualified.push(o.qualified)
        unqualified.push(o.unqualified)
      })
      let option = {
        tooltip: {
          trigger: 'axis',
          axisPointer: { // 坐标轴指示器，坐标轴触发有效
            type: 'shadow' // 默认为直线，可选为：'line' | 'shadow'
          }
        },
        legend: {
          data: ['合格', '不合格']
        },
        grid: {
          left: '3%',
          right: '4%',
          top: '1%',
          bottom: '3%',
          containLabel: true
        },
        xAxis: {
          type: 'category',
          data: xAxis,
          axisLabel: {
            interval: 0,
            rotate: 20,
            formatter: function (value) {
              if (value.length > 5) {
                return value.substring(0, 5) + '...'
              }
              return value
            }
          }
        },
        yAxis: {
          type: 'value'
        },
        series: [{
          name: '合格',
          type: 'bar',
          stack: '总量',
          label: {
            normal: {
              show: true,
              position: 'insideRight'
            }
          },
          data: qualified
        },
        {
          name: '不合格',
          type: 'bar',
          stack: '总量',
          label: {
            normal: {
              show: true,
              position: 'insideRight'
            }
          },
          data: unqualified
        }
        ]
      }
      // 为echarts对象加载数据
      let myChart = this.$echarts.init(document.getElementById('chart_checked'))
      myChart.setOption(option)
    },
    createRandomItemStyle () {
      return {
        normal: {
          color: function color () {
            return (
              'rgb(' + [
                Math.round(Math.random() * 160),
                Math.round(Math.random() * 160),
                Math.round(Math.random() * 160)
              ].join(',') +
                ')'
            )
          }
        }
      }
    }
  },
  watch: {
    activeHy (val, oldVal) {
      this.init()
    },
    activeArea (val, oldVal) {
      this.init()
    }
  }
}

</script>



