<template>
  <div class="col-7">
    <div class="panel">
      <div class="panel-heading border-gray">
        <div class="panel-title">企业投诉TOP5-{{activeArea.name}}</div>
      </div>
      <div class="panel-body">
        <div class="chart" id="chart_punishCount"></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    activeHy: {
      type: Number
    },
    activeArea: {
      type: Object
    }
  },
  data () {
    return {}
  },
  created () {
    this.init()
  },
  methods: {
    init () {
      let self = this
      this.$http.get({
        api: 'A_S_C_COMPLAINT',
        params: {
          industryId: this.activeHy,
          areaId: this.activeArea.code
        },
        callback: (res) => {
          if (res.length === 0) {
            document.querySelector('#chart_punishCount').innerHTML = '暂无数据'
          } else {
            self.draw(res)
          }
        }
      })
    },
    draw (a) {
      let myChart = this.$echarts.init(document.getElementById('chart_punishCount'), 'macarons')
      let xAxis = []
      let yAxis = []
      a.map(o => {
        xAxis.push(o.name)
        yAxis.push(o.value)
      })
      let option = {
        color: ['#3398DB'],
        tooltip: {
          trigger: 'axis',
          axisPointer: { // 坐标轴指示器，坐标轴触发有效
            type: 'shadow' // 默认为直线，可选为：'line' | 'shadow'
          }
        },
        grid: {
          left: '3%',
          right: '4%',
          top: '1%',
          bottom: '2%',
          containLabel: true
        },
        xAxis: [{
          type: 'category',
          data: xAxis,
          axisTick: {
            alignWithLabel: true
          },
          axisLabel: {
            interval: 0,
            rotate: 20,
            formatter: function (value) {
              if (value.length > 5) {
                return value.substring(0, 5) + '...'
              }
              return value
            }
          }
        }],
        yAxis: [{
          type: 'value'
        }],
        series: [{
          name: '',
          type: 'bar',
          barWidth: '60%',
          data: yAxis
        }]
      }
      myChart.setOption(option)
    }
  },
  watch: {
    activeHy (val, oldVal) {
      this.init()
    },
    activeArea (val, oldVal) {
      this.init()
    }
  }
}

</script>



