<template>
  <div class="assessment-r">

    <div class="h_pro_pic_con">
      <div class="fl"><a href="javascript:;" class="h_control" id="rt_pre"><i class="icon-chevron-left"></i></a></div>
      <div class="fm h_pro_pic_outer">
        <div id="rtList">
          <ul>
            <li v-for="(i,index) in hyList" :key="index"><a href="javascript:void(0)" @click="activeHy=i.value"
                :class="{'active': activeHy==i.value}">{{i.name}}</a></li>
          </ul>
        </div>
      </div>
      <div class="fl"><a href="javascript:;" class="h_control" id="rt_next"><i class="icon-chevron-right"></i></a></div>
    </div>

    <div class="col-12">选择时间：
      <el-date-picker v-model="startTime" type="datetime" value-format="yyyy-MM-dd HH:mm:ss" placeholder="请选择开始时间">
      </el-date-picker> - <el-date-picker v-model="endTime" type="datetime" value-format="yyyy-MM-dd HH:mm:ss"
        placeholder="请选择结束时间">
      </el-date-picker>
    </div>
    <p1 :activeHy="activeHy" />

    <p2 :activeHy="activeHy" :startTime="startTime" :endTime="endTime" />

    <p3 :activeHy="activeHy" :startTime="startTime" :endTime="endTime" />

    <p4 :activeHy="activeHy" :startTime="startTime" :endTime="endTime" />
  </div>
</template>

<script>
import P1 from './P1'
import P2 from './P2'
import P3 from './P3'
import P4 from './P4'
export default {
  data () {
    return {
      hyList: [],
      activeHy: -1,
      newProportion: [],
      startTime: '',
      endTime: ''
    }
  },
  components: {
    P1,
    P2,
    P3,
    P4
  },
  created () {
    this.getHy()
  },
  methods: {
    getHy () {
      let self = this
      this.$http.get({
        api: 'A_S_P_BIG_INDUSTRY_LIST',
        params: {},
        callback: (res) => {
          self.hyList = res
        }
      })
    }
  }
}

</script>

<style scoped>
  .assessment-r {
    border: 1px solid #ddd;
    margin-left: 169px;
    background: #fff;
    overflow: hidden;
    padding: 20px;
  }

  .h_pro_pic_con {
    overflow: hidden;
    width: 970px;
    height: auto;
    margin: 0 auto;
    padding-top: 20px;
    margin-bottom: 20px;
  }

  .h_control img {
    margin-top: 27px;
  }

  .h_pro_pic_outer {
    position: relative;
    width: 900px;
    height: 52px;
    overflow: hidden;
    float: left
  }

  .h_pro_pic_outer ul {
    position: absolute;
    width: 100%;
    height: auto;
    left: 0px;
    top: 0px;
  }

  .h_pro_pic_outer ul li {
    width: 150px;
    height: auto;
    line-height: 50px;
    text-align: center;
    float: left;
  }

  .h_pro_pic_outer ul li a {
    display: block;
    margin: 0 10px;
    border: 1px solid #ddd;
    font-size: 16px;
    border-radius: 3px;
  }

  .h_pro_pic_outer ul li a:hover,
  .h_pro_pic_outer ul li a.active {
    background: #058fcd;
    color: #fff;
    border: 1px solid #058fcd
  }

  .h_pro_pic_con .fl a {
    font-size: 30px;
  }

  .h_pro_pic_con_style {
    width: 700px;
    padding-top: 0
  }

  .h_pro_pic_con_style .h_pro_pic_outer {
    width: 626px;
  }

  .h_pro_pic_con_style .h_pro_pic_outer ul li {
    line-height: 40px;
  }

  .h_pro_pic_con_style .h_pro_pic_outer ul li a {
    font-size: 14px;
  }

</style>



