<template>
  <div class="col-7">
    <div class="panel">
      <div class="panel-heading border-warning">
        <div class="panel-title">数量变化趋势</div>
      </div>
      <div class="panel-body">
        <div class="chart" id="chart1">四条线分布代表抽检不合格、召回、维保、投诉</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    activeHy: {
      type: Number
    },
    startTime: {
      type: String
    },
    endTime: {
      type: String
    }
  },
  data () {
    return {}
  },
  created () {
    this.init()
  },
  methods: {
    init () {
      let self = this
      this.$http.get({
        api: 'A_S_P_QUANTITATIVE_TRENDS',
        params: {
          industryId: this.activeHy,
          startTime: this.startTime,
          endTime: this.endTime
        },
        callback: (res) => {
          if (res.series.length === 0) {
            document.querySelector('#chart1').innerHTML = '暂无数据'
          } else {
            self.drawLine(res)
          }
        }
      })
    },
    drawLine (a) {
      a.series.map(o => {
        o.type = 'line'
      })
      let option = {
        tooltip: {
          trigger: 'axis'
        },
        legend: {
          data: a.legend
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        xAxis: {
          type: 'category',
          boundaryGap: false,
          data: a.category
        },
        yAxis: {
          type: 'value'
        },
        series: a.series
      }
      let myChart = this.$echarts.init(document.getElementById('chart1'), 'macarons')
      myChart.setOption(option)
    }
  },
  watch: {
    activeHy (val, oldVal) {
      this.init()
    },
    startTime (val, oldVal) {
      this.init()
    },
    endTime (val, oldVal) {
      this.init()
    }
  }
}

</script>



