<template>
  <div class="col-7">
    <div class="panel">
      <div class="panel-heading">
        <div class="panel-title">产品画像</div>
      </div>
      <div class="panel-body">
        <div class="chart" id="chart4"></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    activeHy: {
      type: Number
    },
    startTime: {
      type: String
    },
    endTime: {
      type: String
    }
  },
  data () {
    return {}
  },
  created () {
    this.init()
  },
  methods: {
    init () {
      let self = this
      this.$http.get({
        api: 'A_S_P_PRODUCTS_WORLDCLOUD',
        params: {
          industryId: this.activeHy,
          startTime: this.startTime,
          endTime: this.endTime
        },
        callback: (res) => {
          if (res.length === 0) {
            document.querySelector('#chart4').innerHTML = '暂无数据'
          } else {
            self.drawBar(res)
          }
        }
      })
    },
    drawBar (a) {
      let option = {
        tooltip: {
          show: true,
          formatter: '{b0}'
        },
        series: [{
          name: '事件画像',
          type: 'wordCloud',
          size: ['80%', '80%'],
          textRotation: [0, 45, 90, -45],
          textPadding: 0,
          autoSize: {
            enable: true,
            minSize: 14
          },
          textStyle: this.createRandomItemStyle(),
          data: a
        }]
      }
      let myChart = this.$echarts.init(document.getElementById('chart4'), 'macarons')
      myChart.setOption(option)
    },
    createRandomItemStyle () {
      return {
        normal: {
          color: function color () {
            return (
              'rgb(' + [
                Math.round(Math.random() * 160),
                Math.round(Math.random() * 160),
                Math.round(Math.random() * 160)
              ].join(',') +
                ')'
            )
          }
        }
      }
    }
  },
  watch: {
    activeHy (val, oldVal) {
      this.init()
    },
    startTime (val, oldVal) {
      this.init()
    },
    endTime (val, oldVal) {
      this.init()
    }
  }
}

</script>



