<template>
  <div class="col-12">
    <div class="panel">
      <div class="panel-heading border-gray">
        <div class="panel-title">行业投诉分析</div>
      </div>
      <div class="panel-body">
        <div class="chart" id="userchart_hy"></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    activeHy: {
      type: Number
    }
  },
  data () {
    return {}
  },
  created () {
    this.init()
  },
  methods: {
    init () {
      let self = this
      this.$http.get({
        api: 'A_S_U_INDUSTRY_COMPLAINTS',
        params: {
          industryId: this.activeHy
        },
        callback: (res) => {
          if (res.length === 0) {
            document.querySelector('#userchart_hy').innerHTML = '暂无数据'
          } else {
            self.draw(res)
          }
        }
      })
    },
    draw (a) {
      a.series.map(o => {
        o.type = 'line'
      })
      let option = {
        tooltip: {
          trigger: 'axis'
        },
        legend: {
          data: a.legend
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        xAxis: {
          type: 'category',
          boundaryGap: false,
          data: a.category
        },
        yAxis: {
          type: 'value'
        },
        series: a.series
      }
      let myChart = this.$echarts.init(document.getElementById('userchart_hy'), 'macarons')
      myChart.setOption(option)
    }
  },
  watch: {
    activeHy (val, oldVal) {
      this.init()
    },
    activeArea (val, oldVal) {
      this.init()
    }
  }
}

</script>



