<template>
  <div class="col-7">
    <div class="panel">
      <div class="panel-heading border-gray">
        <div class="panel-title">企业信息-{{activeArea.name}}</div>
      </div>
      <div class="panel-body">
        <!-- <div class="chart" id="chart_creditCount"></div> -->
        <el-row>
          <el-table :data='item.content' stripe height="260" ref='table' style='width: 100%'
            class="table table-striped">
            <el-table-column prop='id' min-width="200" show-overflow-tooltip label='ID' fixed>
            </el-table-column>
            <el-table-column prop='name' min-width="200" show-overflow-tooltip label='企业'>
            </el-table-column>
            <el-table-column prop='area' width="200" show-overflow-tooltip label='地址' fixed="right">
            </el-table-column>
          </el-table>
        </el-row>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    activeHy: {
      type: Number
    },
    activeArea: {
      type: Object
    }
  },
  data () {
    return {
      item: {},
      nowpage: 1
    }
  },
  created () {
    this.init(1)
  },
  methods: {
    init (page) {
      let self = this
      this.$http.get({
        api: 'A_S_C_ENTERPRISE_INFO',
        params: {
          page: page,
          pageSize: 10,
          industryId: this.activeHy,
          areaId: this.activeArea.code
        },
        callback: (res) => {
          self.item = res
        }
      })
    }
  },
  watch: {
    activeHy (val, oldVal) {
      this.init(1)
    },
    activeArea (val, oldVal) {
      this.init(1)
    }
  }
}

</script>



