<template>
  <div class="col-5">
    <div class="panel">
      <div class="panel-heading">
        <div class="panel-title">今日新增占比情况</div>
      </div>
      <div class="panel-body">
        <div class="list-total" v-if="item.length > 0">
          <dl>
            <dd v-for="(i,index) in item" :key="index">{{i.name}}<p>{{i.value}}</p>
            </dd>
          </dl>
        </div>
        <div class="list-total" v-else>
          暂无数据
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    activeHy: {
      type: Number
    }
  },
  data () {
    return {
      item: []
    }
  },
  created () {
    this.init()
  },
  methods: {
    init () {
      let self = this
      this.$http.get({
        api: 'A_S_P_NEW_PROPORTION',
        params: {
          industryId: this.activeHy
        },
        callback: (res) => {
          self.item = res
        }
      })
    }
  },
  watch: {
    activeHy (val, oldVal) {
      this.init()
    }
  }
}

</script>

<style scoped>
  .list-total dd {
    display: inline-block;
    border: 1px solid #ddd;
    background: #f4f4f4;
    margin: 0 10px 20px;
    padding: 18px 20px 10px;
    width: 45%;
    text-align: center;
  }

  .list-total dd p {
    font-size: 22px;
    color: #058fcd;
    border-top: 1px solid #ddd;
    padding-top: 10px;
    margin-top: 10px;
  }

</style>



