<template>
  <div class="col-5">
    <div class="panel">
      <div class="panel-heading border-warning">
        <div class="panel-title">企业分布</div>
      </div>
      <div class="panel-body" style=" height:665px;">
        <div class="chart" id="chart_map" style=" height:625px;"></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    activeHy: {
      type: Number
    }
  },
  data () {
    return {}
  },
  created () {
    this.init()
  },
  methods: {
    init () {
      let self = this
      this.$http.get({
        api: 'A_S_C_DISTRIBUTION_ENTERPRISE',
        params: {},
        callback: (res) => {
          self.drawMap(res)
        }
      })
    },
    drawMap (a) {
      let self = this
      let myChart = this.$echarts.init(document.getElementById('chart_map'), 'macarons')
      let mapName = 'chongqing'
      let geoCoordMap = {}
      let idName = {}
      let res = []
      let scatterRes = []

      a.map(o => {
        idName[o.name] = o.value
      })
      /* 获取地图数据 */
      myChart.showLoading()
      let mapFeatures = this.$echarts.getMap(mapName).geoJson.features
      myChart.hideLoading()
      mapFeatures.forEach(function (v) {
        // 地区名称
        let name = v.properties.name
        res.push({
          name: name,
          code: v.id,
          value: idName[v.id] ? idName[v.id] : 0
        })
        scatterRes.push({
          name: name,
          value: v.properties.cp
        })
        // 地区经纬度
        geoCoordMap[name] = v.properties.cp
      })
      let option = {
        tooltip: {
          trigger: 'item',
          formatter: function (params) {
            return `${params.name} 企业总数<br> ${params.value ? params.value : 0}家 `
          }
        },
        geo: {
          show: true,
          map: mapName,
          label: {
            normal: {
              show: false
            },
            emphasis: {
              show: false
            }
          },
          roam: true,
          itemStyle: {
            normal: {
              areaColor: '#CCCCCC',
              borderColor: '#3B5077'
            },
            emphasis: {
              areaColor: '#F5D529'
            }
          }
        },
        series: [{
          type: 'map',
          map: mapName,
          geoIndex: 0,
          aspectScale: 0.75, // 长宽比
          showLegendSymbol: false, // 存在legend时显示
          label: {
            normal: {
              show: true
            },
            emphasis: {
              show: false,
              textStyle: {
                color: '#fff'
              }
            }
          },
          roam: true,
          itemStyle: {
            normal: {
              areaColor: '#CCCCCC',
              borderColor: '#3B5077'
            },
            emphasis: {
              areaColor: '#F5D529'
            }
          },
          animation: false,
          data: res
        }, {
          name: '散点',
          type: 'scatter',
          coordinateSystem: 'geo',
          data: scatterRes,
          symbolSize: 1,
          label: {
            normal: {
              formatter: '{b}',
              position: 'center',
              show: true
            },
            emphasis: {
              show: true
            }
          },
          itemStyle: {
            normal: {
              color: '#05C3F9'
            }
          }
        }]
      }
      myChart.setOption(option)
      myChart.on('click', function (params) {
        if (params.data && params.data.name) {
          self.$emit('setActiveArea', params.data)
        }
      })
    }
  },
  watch: {
    activeHy (val, oldVal) {
      this.init()
    }
  }
}

</script>



