<template>
  <div class="sys-main sys-main-style">

    <div class="assessment">
      <div class="assessment-l">
        <a :class="{'active':flowIsData==1}" @click="cmptName='Products';flowIsData=1" href="javascript:void(0)"><i
            class="icon-view-quilt"></i>产品</a>
        <a :class="{'active':flowIsData==2}" @click="cmptName='Company';flowIsData=2" href="javascript:void(0)"><i
            class="icon-view-carousel"></i>企业</a>
        <!-- <a :class="{'active':flowIsData==3}" @click="flowIsData=3" href="javascript:void(0)"><i
            class="icon-assessment"></i>质量事故</a> -->
        <a :class="{'active':flowIsData==4}" @click="cmptName='User';flowIsData=4" href="javascript:void(0)"><i
            class="icon-recent-actors"></i>用户</a>
      </div>

      <!-- <component :is="cmptName"></component> -->
      <products v-if="cmptName=='Products'" />
      <company v-if="cmptName=='Company'" />
      <user v-if="cmptName=='User'" />
    </div>
  </div>
</template>

<script>
import Products from './components/Products'
import Company from './components/Company'
import User from './components/User'
export default {
  data () {
    return {
      flowIsData: 1,
      cmptName: 'Products'
    }
  },
  components: {
    Products,
    Company,
    User
  },
  created () {},
  methods: {},
  mounted () {}
}

</script>

<style scoped>
  .assessment {
    position: relative;
  }

  .assessment-l {
    position: fixed;
    left: 21px;
    top: 120px;
  }

  .assessment-l a {
    display: block;
    background: #fff;
    font-size: 16px;
    color: #666;
    border: 1px solid #ddd;
    margin-bottom: -1px;
    padding: 15px 20px;
    width: 170px;
  }

  .assessment-l a i {
    margin-right: 10px;
    font-size: 18px;
  }

  .assessment-l a.active {
    background: #058fcd;
    color: #fff
  }

</style>



